	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Christopher's First Interview","date":"24 Jan 2002","slug":"/2002-01-24-christophers-first-interview/","tags":"Y2002"},"html":"<p>Today Christopher had an entry interview for Kindergarten. Unlike in the States, where Kindergarten is a year of half-day school before first grade, here it's a kind of state-sponsored day care available starting at age 3. In theory, every child is guaranteed a half-day place in Kindergarten. In practice, you have to apply for a spot early, and you may not get the location or the time slot you want. The cost and quality can vary widely from locality to locality. In Jesteburg the cost is based on income, so as double-earners we're at the high end of the scale. There are full-day groups available, with 25 children per caretaker. The cost is nearly as high as our current private day care, where there are never more than 10 children, and usually no more than 6. I think that Christopher can wait until he is at least 4 until he starts Kindergarten.</p>"},"next":{"fields":{"title":"Unenemployed, And Doing Nothing About It","date":"23 Jan 2002","slug":"/2002-01-23-unenemployed-and-doing-nothing-about-it/"}},"previous":{"fields":{"title":"Schilly To Stay","date":"24 Jan 2002","slug":"/2002-01-24-schilly-to-stay/"}}},{"node":{"fields":{"title":"Unenemployed, And Doing Nothing About It","date":"23 Jan 2002","slug":"/2002-01-23-unenemployed-and-doing-nothing-about-it/","tags":"Y2002"},"html":"<p>This is was quick 2 paragraphs in the <a href=\"http://www.abendblatt.de/contents/ha/news/politik/html/220102/01ARB226.HTM\">local paper</a>: a study by the Employment Ministry found that of Germany's 4.3 million unemployed, 25% are making no efforts whatsoever to find work. On the other hand, 50% are making 'intensive' efforts to find a job.</p>"},"next":{"fields":{"title":"They're Getting What They Deserve","date":"23 Jan 2002","slug":"/2002-01-23-theyre-getting-what-they-deserve/"}},"previous":{"fields":{"title":"Christopher's First Interview","date":"24 Jan 2002","slug":"/2002-01-24-christophers-first-interview/"}}},{"node":{"fields":{"title":"They're Getting What They Deserve","date":"23 Jan 2002","slug":"/2002-01-23-theyre-getting-what-they-deserve/","tags":"Y2002"},"html":"<p>I'll let others comment on the treatment of the detainees held by the US in Cuba. <a href=\"http://www.spiegel.de/politik/ausland/0,1518,178536,00.html\">Spiegel Online</a> has an interesting article on the American reaction to the conditions in Cuba (see headline) and to the international criticism. It concludes (my translation): \"The USA doesn't exactly suffer from modesty. It never tires of proclaiming itself to be world's best democracy,  with the world's finest constitution. It shouldn't be surprised when it is measured against its own words.\"</p>"},"next":{"fields":{"title":"NPD Ban Backfires","date":"23 Jan 2002","slug":"/2002-01-23-npd-ban-backfires/"}},"previous":{"fields":{"title":"Unenemployed, And Doing Nothing About It","date":"23 Jan 2002","slug":"/2002-01-23-unenemployed-and-doing-nothing-about-it/"}}}],"pathPrefix":"","first":false,"last":false,"index":938,"pageCount":1101}}

